<template>
  <div class="article">
    <div class="article-content">
      <h6 class="article-title">{{article.title}}</h6>
      <ul class="article-tags">
        <li class="article-tag-item" v-for="(tag, index) in article.tags" :key="index">{{tag.content}}</li>
      </ul>
      <p class="article-post-time">{{article.updateTime | formatTime}}</p>
      <div class="article-txt" v-html="article.content"></div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {getQuery, formatDate} from 'common/script/util.js';


  export default {
    data() {
      return {
        articleId: -1,
        article: {}
      };
    },
    beforeCreate() {
      console.log('article created');
      this.articleId = getQuery(location.search).articleId;

      this.$http.get('/khachiyanBlog/articles/' + this.articleId)
        .then((res) => {
          this.article = res.body;
          console.log(this.article);
        })
        .catch((err) => {
          console.log(err);
        });

    },
    filters: {
      formatTime(value) {
        if(value) {
          return formatDate(value, 'yyyy-MM-dd hh:mm:ss');
        }else {
          return '';
        }
      }
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus" scope>
  .article
    position: fixed
    top: 0
    left: 0
    width: 100%
    height: 100%
    background-color: #fff
    .article-content
      width: 800px
      margin: 20px auto 0 auto
      .article-title
        font-size: 20px
      .article-tags
        margin-top: 15px
        font-size: 0
        .article-tag-item
          display: inline-block
          line-height: 20px
          padding: 0 5px
          margin-left: 5px
          color: #999
          font-size: 12px
          border: 1px solid #999
          border-radius: 2px
          &:first-child
            margin-left: 0
      .article-post-time
        margin-top: 20px
        font-size: 14px
        color: #666
      .article-txt
        margin-top: 30px
        font-size: 14px
</style>



